.footer {
    background-color: #1e1e1e;
    color: white;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    min-height: 70px;
    border-radius: 8px;
    padding: 20px 25px;
}

.footer .footer_copyright {
    font-size: small;
    text-align: end;
    color: #9c9c9c;
}

.footer .footer_copyright > a {
    text-decoration: none;
    color: var(--main-color); 
}

.fa-beat {
    color: tomato;
    margin: 2px;
    animation: fa-beat 5s ease infinite;
  }
  
  @keyframes fa-beat {
    0% {
      transform: scale(0.5);
    }
    5% {
      transform: scale(0.75);
    }
    20% {
      transform: scale(0.5);
    }
    30% {
      transform: scale(0.5);
    }
    35% {
      transform: scale(0.75);
    }
    50% {
      transform: scale(0.5);
    }
    55% {
      transform: scale(0.75);
    }
    70% {
      transform: scale(0.5);
    }
  }

