/* About Me Section */
.aboutme_text {
    color: darkslategray;
    font: smaller;
    line-height: 22px;
    white-space: pre-line;
}

/* End of about me section */

/* Timeline section */

.resume_timeline .timeline_content .timeline_title {
    font-size: 16px !important;
    font-weight: 500 !important;
}

.resume_timeline .timeline_content .timeline_date {
    color: darkslategray;
}

.resume_timeline .timeline_content .timeline_description {
    color: gray;
    font-size: small;
    margin-top: 6px;
}

/* End of Timeline section */

/* Services */
.service {
    height: 100%;
    text-align: center;
}

.service .service_title {
    font-size: 16px;
    font-weight: 500;
    line-height: 35px;
    text-align: center;
}

.service .service_description {
    color: #989898;
    border-color: var(--main-color);
    margin-right: 5px;
    text-align: center;
}

.service_icon > .MuiSvgIcon-root {
    font-size: 46px;
    margin-left: 0;
    font-weight: 300;
    color: darkslategray;
}
/* End of Services */

/* Skills */

.skill {
    border-radius: 8px !important;
    padding: 20px;
    margin: 10px;
    box-shadow: 0 0 48px 0 rgba(4,6,4,0.08) !important;
    min-height: 186px;
}

.skill .skill_title {
    font-size: 14px !important;
    font-weight: 500px !important;
    line-height: 35px !important;
}

.skill .skill_description {
    display: flex;
    align-items: center;
    color: #989898 !important;
    font-weight: 400 !important;
    line-height: 18px !important;
}

.skill .skill_description .timeline_dot {
    border-color: var(--main-color) !important;
    padding: 2px !important;
    margin-right: 5px !important;
}

/* End of Skills */

/*  Contant Section*/

.contactInfo_item {
    font-size: 14px !important;
    color: #787878;
    line-height: 34px;

}

.contactInfo_item > span {
    color: black;
    font-weight: 500;
}

.contactInfo_socialsContainers {
    margin-top: 25px;
}

.contactInfo_social > a > .MuiSvgIcon-root {
    font-size: 16px !important;
    margin-right: 10px;
    color: black;
}
/* End of Contact Section */